/* точки от заказчика
1920 пикселей,
1366 пикселей,
1024 пикселя,
366 пикселей.
*/
/*media-query*/
*, *:after, *:before {
  vertical-align: baseline;
  font-size: 100%;
  border: 0 none;
  outline: 0 none;
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html, body {
  width: 100%;
  height: 100%;
  font-family: 'ProximaNova', Arial, sans-serif;
  color: black;
  font-size: 16px;
  font-weight: 300;
  line-height: 26px; }

ul, li {
  list-style-type: none; }

a {
  text-decoration: none;
  color: black; }

img {
  max-width: 100%; }

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle; }

textarea {
  resize: none; }

.page {
  overflow-x: hidden; }

.container {
  width: 100%;
  max-width: 1110px;
  margin: 0 auto;
  padding: 0 20px; }
  .container--min {
    max-width: 1000px; }

.wrapper {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  position: relative; }

.laptop {
  display: none !important; }
  @media (max-width: 1365px) {
    .laptop {
      display: block !important; } }

.laptop-lg {
  display: none !important; }
  @media (max-width: 1023px) {
    .laptop-lg {
      display: block !important; } }

.mobile {
  display: none !important; }
  @media (max-width: 768px) {
    .mobile {
      display: block !important; } }

@media (max-width: 1023px) {
  .hide-laptop-lg {
    display: none !important; } }

@media (max-width: 768px) {
  .hide-mobile {
    display: none !important; } }

.footer {
  background: #1a1a1a;
  color: yellow; }

.footer-social {
  background: blue;
  color: yellow; }

.header {
  background: #fff; }
  @media (max-width: 761px) {
    .header {
      background: red; } }
  .header__wrap {
    display: flex; }
  .header_width-700 {
    width: 700px; }
  .header_color-green {
    background: green; }

.slider {
  background: yellowgreen; }
  .slider__item-img {
    border-radius: 25px; }

/*
 Навигация

 section-slider
 section-promo
 section-products
 section-promotion
 section-clients
 section-presscenter
 section-response
 section-info

*/
.index__slider-list {
  height: 100%; }
  .index__slider-list .slick-list, .index__slider-list .slick-track {
    height: 100%; }

.page--index .header_main .header__shop {
  display: none; }
